<template>
	<view class="list">		
		<view class="fixbg" :style="{ 'background-image': 'url('+playlist.coverImgUrl+')'}"></view>
		<musichead title="歌单" :icon="true" color="white"></musichead>
		<view class="container" v-show="!isLoading2">
			
			<scroll-view scroll-y="true">
				<!--榜单简介-->
				<view class="list-head">
					<!--榜单图片-->
					<view class="list-head-img">
						<image :src="playlist.coverImgUrl" mode=""></image>
						<text class="iconfont icon-yousanjiao">{{playlist.playCount | formatCount}}</text>
					</view>
					<!--榜单文字简介-->
					<view class="list-head-text">
						<view>{{ playlist.name}}</view>
						<view>
							<image src="../../static/disc.png" mode=""></image>扣扣云音乐
						</view>
						<view>
							{{playlist.description}}
						</view>
					</view>
				</view>
				<!-- #ifdef MP-WEIXIN -->
				<button class="list-share" open-type="share">
					<text class="iconfont icon-fenxiang"></text>分享给微信好友
				</button>
				<!-- #endif -->	
				<!--歌曲数目-->
				<view class="list-music">
					<view class="list-music-head">
						<text class="iconfont icon-bofang1"></text>
						<text>播放全部</text>
						<text>(共{{playlist.trackCount}}首)</text>
					</view>
					<!--数据列表-->
					<view class="list-music-item" v-for="(item,index) in playlist.tracks" :key="index">
						<view class="list-music-top">{{index+1}}</view>
						<view class="list-music-song" @tap="handleToDetail(item.id)">
							<view>{{item.name}}</view>
							<view>
								<image v-if="privileges[index].flag >60 && privileges[index].flag <70"
									src="../../static/dujia.png" mode=""></image>
								<image v-if="privileges[index].maxbr == 999000" src="../../static/SQ.png" mode="">
								</image>
								{{item.ar[0].name}} - {{item.name}}
							</view>
						</view>
						<text class="iconfont icon-bofang" @tap="handleToDetail(item.id)"></text>
					</view>
				</view>
				<view class="standView"></view>
				<bottom-bar></bottom-bar>
			</scroll-view>
		</view>
	</view>
</template>

<script>
	// import mForSkeleton from "@/components/m-for-skeleton/m-for-skeleton";
	import {
		list,
		userInfo,
		userCollectMusicId,
		userCollectMusic,
		userCheckCollectMusicId,
		userDisCollectMusic
	} from '../../common/api.js'
	export default {
		data() {
			return {
				playlist: {
					coverImgUrl: '',
					trackCount: '',
					creator: {
						avatarUrl: '',
						nickname: '',
					},
					// isCollect:-1,
				},
				privileges: [],
				isLoading: true,
				useremail: '',
				// statusList: [],
				// kkk:[],
				isLoading2: true,
			}
		},
		computed: {
			// check() {
			// 	return function(index) {
			// 		// console.log(this.statusList[index])\
			// 		if (this.statusList[index] == 'true')
			// 			return true;
			// 		else return false;
			// 		// return this.statusList[index];
			// 	}
			// }
		},
		onLoad(options) {
			// console.log(options.listId);
			uni.showLoading({
				title: '正在加载...',
			});
			// 获取榜单
			list(options.listId).then((res) => {
				// console.log(res)
				if (res[1].data.code == '200') {
					// 获取榜单列表
					this.playlist = res[1].data.playlist;
					// console.log(this.playlist);
					// 获取歌曲权限
					this.privileges = res[1].data.privileges;
					// console.log(this.privileges);
					// 将歌单缓存
					this.$store.commit('INIT_TOPLISTIDS', res[1].data.playlist.trackIds);
					this.isLoading2 = false;
					uni.hideLoading();
				}
			});
		},
		methods: {
			// 转到歌曲详情
			handleToDetail(songId) {
				uni.removeStorage({
					key: 'song_list',
				});
				let item;
				let song_list = [];
				for (item in this.playlist.tracks) {
					song_list[item] = this.playlist.tracks[item].id;
				};
				// console.log(song_list);
				// 缓存歌单
				uni.setStorage({
					key: 'song_list',
					data: song_list,
				});
				// 转到歌曲详情
				uni.navigateTo({
					url: '/pages/detail/detail?songId=' + songId
				});
			},
			// // 收藏音乐
			// collect(musicid) {
			// 	if (!this.$u.utils.isLogin()) return
			// 	else {
			// 		this.$u.toast('收藏成功');
			// 		userCollectMusic(this.useremail, musicid);
			// 	}
			// },
			// // 取消收藏音乐
			// discollect(musicid) {
			// 	if (!this.$u.utils.isLogin()) return
			// 	else {
			// 		this.$u.toast('取消收藏成功');
			// 		userDisCollectMusic(this.useremail, musicid);
			// 	}
			// }
		}
	}
</script>

<style scoped>
	.list {}

	.list-head {
		display: flex;
		margin: 30rpx;
	}

	.list-head-img {
		width: 264rpx;
		height: 264rpx;
		border-radius: 30rpx;
		overflow: hidden;
		position: relative;
		margin-right: 42rpx;
	}

	.list-head-img image {
		width: 100%;
		height: 100%;
	}

	.list-head-img text {
		position: absolute;
		right: 8rpx;
		top: 8rpx;
		color: white;
		font-size: 26rpx;
	}

	.list-head-text {
		flex: 1;
		color: #F0F2F7
	}

	.list-head-text view:nth-child(1) {
		color: white;
		font-size: 34rpx;
	}

	.list-head-text view:nth-child(2) {
		display: flex;
		margin: 20rpx 0;
		font-size: 24rpx;
		align-items: center;
	}

	.list-head-text view:nth-child(2) image {
		width: 54rpx;
		height: 54rpx;
		border-radius: 50%;
		margin-right: 14rpx;
	}

	.list-head-text view:nth-child(3) {
		line-height: 34rpx;
		font-size: 22rpx;
	}

	.list-share {
		width: 330rpx;
		height: 74rpx;
		margin: 0 auto;
		background: rgba(0, 0, 0, 0.4);
		border-radius: 37rpx;
		color: white;
		text-align: center;
		line-height: 74rpx;
		font-size: 28rpx;
	}

	.list-share text {
		margin-right: 16rpx;
	}

	.list-music {
		background: white;
		border-radius: 50rpx;
		margin-top: 40rpx;
		overflow: hidden;
	}

	.list-music-head {
		height: 50rpx;
		margin: 30rpx 0 70rpx 22rpx;
	}

	.list-music-head text:nth-child(1) {
		height: 50rpx;
		font-size: 50rpx;
	}

	.list-music-head text:nth-child(2) {
		font-size: 30rpx;
		margin: 0 10rpx 0 26rpx;
	}

	.list-music-head text:nth-child(3) {
		font-size: 26rpx;
		color: #b2b2b2;
	}

	.list-music-item {
		display: flex;
		margin: 0 32rpx 66rpx 46rpx;
		align-items: center;
		color: #959595;
	}

	.list-music-top {
		width: 58rpx;
		font-size: 30rpx;
		line-height: 30rpx;
	}

	.list-music-song {
		flex: 1;
	}

	.list-music-song view:nth-child(1) {
		font-size: 28rpx;
		color: black;
		width: 70vw;
		white-space: nowrap;
		overflow: hidden;
		text-overflow: ellipsis;
	}

	.list-music-song view:nth-child(2) {
		/* display: flex; */
		flex-wrap: wrap;
		font-size: 20rpx;
		align-items: center;
		width: 70vw;
		white-space: nowrap;
		overflow: hidden;
		text-overflow: ellipsis;
	}

	.list-music-song view:nth-child(2) image {
		width: 32rpx;
		height: 24rpx;
		margin-right: 10rpx;
		flex-shrink: 0;
	}

	.list-music-song text {
		font-size: 50rpx;
		color: #c7c7c7;
	}

	.list-music-collect {
		height: 16px;
		width: 16px;
	}
	.standView{
		width: 100%;
		height: 150rpx;
	}
</style>
